/**
 * @Doc https://hexo.io/zh-cn/docs/configuration.html
 */

export class Site {
  title = ''; // 
  subtitle = ''; // 

  /**
   * descriptionSEOauthor
   */
  description = ''; // 
  author = ''; // 
  language = ''; // 
  timezone = ''; //Hexo America/New_York, Japan,  UTC 
}

export class URL {
  url = ''; // 
  root = ''; // 
  permalink = ''; //   
  permalink_defaults = ''; // 
}

export class Directory {
  source_dir = ''; // 
  public_dir = ''; // 
  tag_dir = ''; // 
  archive_dir = ''; // 
  category_dir = ''; // 
  code_dir = ''; // Include code 
  i18n_dir = ''; // i18n
  skip_render = ''; //  glob 
}

export class Writing {
  new_post_name = ''; // 
  default_layout = ''; // 
  titlecase = false; //  title case
  filename_case = 0; //  (1)  (2) 
  external_link = ''; // 
  render_drafts = false; // 
  post_asset_folder = false; //  Asset 
  relative_link = false; // 
  future = true; // 
  highlight: {
    enable: boolean,
    line_number: boolean,
    auto_detect: boolean,
    tab_replace: string
  } | null = null; // 

}

export class CategoryAndTags {
  default_category = ''; // 
  category_map = ''; // 
  tag_map = ''; // 
}

export class DateTimeFormat {
  date_format = ''; // 	YYYY-MM-DD
  time_format = ''; // 	H:mm:ss
}

export class Pagination {
  per_page = 0; //  (0 = )
  pagination_dir = ''; // 
}

export class Extensions {
  theme: string | boolean = false; // false
  deploy = {}; // 
}

export class Theme {
  menu = {};
  menu_icons = {
    enable: false
  };
  social = {};
  social_icons = {
    enable: false
  };

  avatar = {
    enable: false,
    url: ''
  };
  powered_by = '';
}




